.location-backdrop {
    background-color: rgba(174, 198, 165, 0.3);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.location-modal {
    width: 280px;
    height: 365px;
    gap: 20px;
    background-color: var(--secondary-text-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid var(--default-color);
    border-radius: 10px;
    padding: 28px;
    display: flex;
    align-self: center;
    flex-direction: column;
}

.modal-btn-close__icon {
    width: 16px;
    height: 16px;
}

.modal-btn-close {
    background-color: transparent;
    padding: 0;
    position: absolute;
    top: 6px;
    right: 6px;
}

.location-modal__text {
    text-transform: capitalize;
    font-family: 'Seymour One', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: var(--primary-text-color);
    text-align: center;
}

.location-modal__img {
    border-radius: 10px;
    width: 224px;
    height: 199px;
}

.location-modal__link {
    background-color: transparent;
    padding: 0;
    background-color: var(--default-color);
    border-radius: 10px;
    width: 134px;
    height: 42px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    color: var(--primary-text-color);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    align-self: center;
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1),
        color 300ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        background-color: var(--hover-color);
    }

    &:focus {
        background-color: var(--hover-color);
        color: var(--secondary-text-color);
    }
}

@media screen and (min-width: 768px) {
    .location-modal {
        width: 568px;
        height: 458px;
        gap: 32px;
        padding: 32px 60px 32px 60px;
    }

    .location-modal__text {
        font-size: 20px;
        line-height: 1.4;
    }

    .modal-btn-close {
        top: 18px;
        right: 18px;
    }

    .modal-btn-close__icon {
        width: 20px;
        height: 20px;
    }

    .location-modal__img {
        width: 448px;
        height: 260px;
    }

    .location-modal__link {
        width: 145px;
        font-size: 14px;
        line-height: 1.21;
    }
}

@media screen and (max-width: 479px) {
    .location-modal {
        max-width: 100%;
    }
}